import axios from 'axios';
import apiMovie from './api-movie.js';
import { getMovieGenres } from './api-genres.js';
import { createMovieCard } from './markup.js';
import { openModal } from './modal.js';
import { createPaginationButtons } from './pagination.js';

let currentPage = 1;

// Funcia pentru ataarea event listener-ului la un card de film:
function attachCardClickListener(movieCard, movieId) {
  movieCard.addEventListener('click', () => {
    openModal(movieId); // Deschide fereastra modal cnd este apsat un card de film.
  });
}

// Funcia pentru eliminarea event listener-ului de pe un card de film:
function removeCardClickListener(movieCard) {
  movieCard.removeEventListener('click', openModal);
}

// Cnd reconstruim cardurile pentru o alt pagin, eliminam event listenerii vechi nainte de a ataa noi asculttori.
// Funcia pentru afiarea cardurilor de filme:
async function displayMoviesByPage(page) {
  const movieContainer = document.querySelector('.movie-container');
  movieContainer.innerHTML = ''; // Cur cardurile de filme existente.

  try {
    const movies = await getMoviesFromApi(page);
    const genres = await getMovieGenres(); // Obine genurile

    // Eliminarea event listener-ilor de pe cardurile vechi nainte de adugarea noilor carduri:
    const movieCards = document.querySelectorAll('.movie-card');
    movieCards.forEach(movieCard => {
      removeCardClickListener(movieCard);
      movieCard.remove();
    });

    movies.forEach(movie => {
      const movieCard = createMovieCard(movie, genres);
      movieContainer.appendChild(movieCard);
      attachCardClickListener(movieCard, movie.id);
    });
  } catch (error) {
    console.error(
      'There was a problem displaying movies for page:',
      page,
      error
    );
  }
}

// Actualizeaz funcia getMoviesFromApi pentru a primi pagina ca argument:
async function getMoviesFromApi(page) {
  try {
    const apiMoviePage = {
      ...apiMovie,
      url: `${apiMovie.url}&page=${page}`, // Adaug pagina la URL-ul cererii.
    };

    const response = await axios.request(apiMoviePage);

    if (response.status !== 200) {
      throw new Error('Network response was not ok');
    }

    return response.data.results;
  } catch (error) {
    console.error('There was a problem fetching movies for page:', page, error);
    return [];
  }
}

// Funcia pentru actualizarea paginrii i afiarea cardurilor pentru pagina curent:
async function updatePaginationAndDisplay(page) {
  try {
    currentPage = page;
    await displayMoviesByPage(currentPage);

    // Utilizarea funciei din fiierul pagination.js pentru a crea butoanele de paginare
    createPaginationButtons(currentPage, updatePaginationAndDisplay);
  } catch (error) {
    console.error('There was a problem updating pagination:', error);
  }
}

// Apelul iniial pentru afiarea cardurilor pentru prima pagin:
updatePaginationAndDisplay(currentPage);
